* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montseratt', sans-serif;
}

.app {
  background-image: url('./Assets/open.gif');
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
}

.app.thunder {
  background-image: url('./Assets/thunder.gif');
}
.app.rain {
  background-image: url('./Assets/rain.gif');
}
.app.light-rain {
  background-image: url('./Assets/rain\ light.gif');
}
.app.snow {
  background-image: url('./Assets/snow.gif');
}
.app.haze {
  background-image: url('./Assets/haze.gif');
}
.app.clear {
  background-image: url('./Assets/clear.gif');
}
.app.clouds {
  background-image: url('./Assets/clouds.gif');
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75)
  );
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px;
  /* margin-top: -25px; */

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 0.4s ease-in;
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(50, 50, 70, 0.5);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
}

.message {
  color: #fff;
  text-align: center;
  font-size: 30px;
}

.origin {
  color: #fff;
  font-size: 32px;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);

  position: absolute;
  bottom: 0%;
}

.origin a {
  text-decoration: none;
  appearance: none;
  color: #fff;
}
